window.$ = window.jQuery = function(selectorOrArray){
    let elements
    const Ã¸ = Object.create(null)
    if(typeof selectorOrArray === "string"){
         elements = document.querySelectorAll(selectorOrArray)
    }else if(selectorOrArray instanceof Array){
         elements = selectorOrArray
    }

    // api å¯ä»¥æä½elements
    return {
        
        find(selector){
            let array = []
            for(let i=0; i<elements.length;i++){
                const elements2 = Array.from(elements[i].querySelectorAll(selector))
                array = array.concat(elements2)
            }
            array.oldApi = this  // thiså°±æ¯api
            return jQuery(array)
        },
        each(fn){
            for(let i=0;i<elements.length;i++){
                fn.call(Ã¸,elements[i],i)
            }
            return this
        },
        parent(){
            const array = []
            this.each((node)=>{
                if(array.indexOf(node.parentNode) === -1){
                    array.push(node.parentNode)
                }
            })
            return jQuery(array)
        },
        children(){
            const array = []
            this.each((node)=>{
                if(array.indexOf(node.children) === -1){
                    array.push(...node.children) // å±å¼æä½ç¬¦
                }
            })
            return jQuery(array)
        },
        print(){
            console.log(elements)
        },
        siblings(){ // é¤äºèªå·±çåå¼å§å¦¹
            const array = []
            this.each((node)=>{
                if(array.indexOf(node.parentNode.children)=== -1){
                        array.push(...Array.from(node.parentNode.children).filter(n => n!==node))
                }
            })
            return jQuery(array)
        },
        // é­åï¼å½æ°è®¿é®å¤é¨åé
        addClass: function(className){
            for(let i=0; i<elements.length;i++){
                elements[i].classList.add(className)
            }
            return this
        },
        oldApi: selectorOrArray.oldApi,
        end(){
            return this.oldApi // æ°api
        },
    }
}


